import { useState } from "react";

const useFetch = (props) => {
  const [response, setResponse] = useState({
    status: "",
    message: "",
    username: "",
    calendars: [{ calendarid: "", c_name: "", description: "", color: "" }],
  });

  const [loading, setLoading] = useState(false);
  const doFetch = () => {
    console.log(props.url);
    // setResponse({
    //   calendars: [
    //     {
    //       calendarid: "Hola",
    //       c_name: "Hola",
    //       description: "Hola",
    //       color: "Hola",
    //     },
    //     {
    //       calendarid: "Calendario2",
    //       c_name: "Calendario2",
    //       description: "Calendario2",
    //       color: "Calendario2",
    //     },
    //   ],
    // });
    // return;
    setResponse({ status: "" });
    setLoading(true);
    const options = {
      method: props.method,
      body: props.formData,
    };
    fetch(props.url, options)
      .then((resp) => {
        return resp.json();
      })
      .then((resp) => {
        setLoading(false);
        console.log(resp);
        setResponse(resp);
      })
      .catch(() => {
        setLoading(false);
        setResponse({ status: "500 Internal Server Error" });
      });
  };
  return { response, loading, doFetch };
};
export default useFetch;
